import {  Button } from "react-bootstrap"
import Modal from "react-bootstrap/Modal";
import ModalBody from "react-bootstrap/ModalBody";
import ModalHeader from "react-bootstrap/ModalHeader";
import ModalFooter from "react-bootstrap/ModalFooter";
import ModalTitle from "react-bootstrap/ModalTitle";

export const AddPrerequisite = ({show, handleClose}) => {
    const file_path = "http://localhost/app_server_curriculum_app/server/actions/courses/get_all_courses.php";
    fetch(file_path)
    .then((res) => {
        // console.log(res)
        // console.log(res);
        return res.json()
    })
    .then(data => {
        console.log(data)
    })
  return (
    <>
        <Modal show={show} onHide={handleClose} animation={false}>
            <ModalHeader closeButton>
                <ModalTitle>Modal heading</ModalTitle>
            </ModalHeader>
            <ModalBody>Woohoo, you're reading this text in a modal!</ModalBody>
            <ModalFooter>
                <Button variant="secondary" onClick={handleClose}>
                    Close
                </Button>
            </ModalFooter>
        </Modal>
    </>
  )
}
